  width: 100%;
  padding: calc(0.75rem - 0.125em - 1px) 0.75rem calc(0.75rem + 0.125em - 1px);
  transition: border-color ease 200ms;
}

.mc-select::-ms-expand {
  display: none;
}

.mc-select.is-hover,
.mc-select:hover {
  border-color: #222020;
}

.mc-select.is-focus,
.mc-select:focus {
  border-color: #25a8d0;
}

.mc-select:disabled {
  background: #eeeef0 url('<svg xmlns="http://www.w3.org/2000/svg" fill="#887f87" viewBox="0 0 24 24"><path d="M12.71 17.21a1 1 0 0 1-1.42 0l-9-9a1 1 0 1 1 1.42-1.42l8.29 8.3 8.29-8.3a1 1 0 1 1 1.42 1.42z"/></svg>') right 1rem center no-repeat;
  background-size: 1.5rem;
  border-color: #eeeef0;
  color: #887f87;
  cursor: default;
}

.mc-select.is-invalid {
  border-color: #df382b;
}

.mc-select.is-valid {
  border-color: #78be20;
}

/* mqp:end */
